main {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.loading {
    width: 100%;
    min-height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pokemon-details {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.card-single-left {
    width: 30%;
    height: 400px;
    padding: 1.5rem 0rem;
    background-color: #fff;
    box-shadow: 2px 2px 10px rgba(86, 108, 214, .15);
    border-radius: 16px;
    flex-wrap: nowrap;
}

.card-footer-details {
    display: flex;
    justify-content: center;
    img {    
        margin: 0 2rem;
    }
}

.card-footer-details-right {
    display: flex;
    justify-content: flex-start;
    img {
        margin-right: .5rem;
    }
}


.card-body-details {
    padding: 1.3rem 1rem;
    display: flex;
    flex-direction: column;
    color: $text-color;
    span {
        margin-bottom: 1rem;
    }
}

.card-pokemon-details {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-pokemon-details img{
    width: 200px;
    text-align: center;
}

.card-header-details {
    display: flex;
    flex-direction: column;
    color: $text-color;

    h2 {
        font-size: 1.8rem;
        font-weight: 600;
        padding: 0 1rem;
        text-transform: lowercase;
    }

    h2:first-letter {
        text-transform: uppercase;
    }
}

.card-footer-details {
    padding: 0 1rem;
    text-align: center;
}

.badge-details {
    padding: .2rem .5rem;
    border-radius: 20px;
    font-weight: 700;
    font-size: .7rem;
    font-weight: 300;
    font-size: .8rem;
    color: #fff;
    display: flex;
    justify-content: flex-start;
}

.card-single-right {
    width: 60%;
    padding: 3rem 3rem;
    background-color: #fff;
    box-shadow: 2px 2px 10px rgba(86, 108, 214, .15);
    border-radius: 16px;
}

.search-wrapper-detail {
    width: 50%;
}

.search-wrapper-detail form{
    display: flex;
    justify-content: flex-end;
    
}

.search-wrapper-detail button{
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    
}

.search-wrapper-detail input{
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
}

.search-wrapper-detail > .search-input-detail {
    width: 280px;
}

.search-input-detail {
    background: none;
    outline: none;
    font-size: 1rem;
    padding-left: 30px;
    height: 40px;
    border-radius: 50px;
    box-shadow: 2px 2px 10px rgba(86, 108, 214, .15);
    width: 340px;
}


@media only screen and (max-width: 860px) {

    .pokemon-details {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .card-single-left, .card-single-right {
        justify-content: center;
        width: 80%;
        margin: 1rem 0rem;
    }

    .card-header-details {
        align-items: center;
    }

}


@media only screen and (max-width: 450px) {

    .search-container {
        display: flex;
        justify-content: center;
    }

    .card-single-left {
        width: 94%;
        height: 400px;
        padding: 1.5rem 0rem;
    }

    .card-single-right {
        width: 94%;
        padding: 1.5rem 0rem;
    }

    .search-wrapper-detail {
        display: flex;
        justify-content: flex-end;
        width: 300px;
    }

    .search-wrapper-detail > .search-input-detail {
        width: 180px;
    }

}

@media only screen and (max-width: 425px) {
    .search-input-detail {
        width: 300px;
    }
}

@media only screen and (max-width: 375px) {
    
    .search-input-detail {
        width: 260px;
    }
}

@media only screen and (max-width: 320px) {
    .search-input-detail {
        width: 200px;
    }
}