
.search-pokemon {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 80px;
    margin-bottom: .5rem;
}
.notfound {
    text-align: center;
    color: rgb(165, 11, 11);
}

.search-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.back-button {
    width: 50%;
}

.back-btn {
    display: flex;
    justify-content: center;
    width: 60px;
    height: 40px;
    border-radius: 40px;
    cursor: pointer;
    background-color: $main-color;
}

.back-btn img {
    width: 30px;
}

.search-wrapper {
    width: 50%;
}

.search-wrapper form {
    display: flex;
    justify-content: flex-end;
    
}

.search-wrapper button {
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    
}

.search-wrapper > .search-input {
    width: 280px;
}

.search-input {
    background: none;
    outline: none;
    font-size: 1rem;
    padding-left: 30px;
    height: 40px;
    border-radius: 50px;
    box-shadow: 2px 2px 10px rgba(86, 108, 214, .15);
    width: 340px;
}

.search-button {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 60px;
    height: 40px;
    border-radius: 40px;
    cursor: pointer;
    background-color: $main-color;
}

.search-button img {
    width: 30px;
}



.home-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 3rem;
    grid-gap: 2rem;
}

.pagination-content {
    width: 100%;
    margin: 2rem 0;
}
.pagination {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: auto;
    list-style: none;
    color: $text-color;
}

.pagination-link {
    width: 40px;
}

.pagination-link:hover{
    cursor: pointer;
}

.pagination-active-link a{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $main-color;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFF;
    font-weight: 600;
}

.pagination-next-link, .pagination-previous-link{
    width: 40px;
    height: 40px;
}

.pagination-next-link:hover, .pagination-previous-link:hover {
    cursor: pointer;
}

@media only screen and (max-width: 860px) {

    .home-cards {
        grid-template-columns: repeat(2, 1fr);
    }

}

@media only screen and (max-width: 600px) {
    .main-content {
        margin: 0;
    }
    
    .pagination-content {
        width: 40%;
    }

}

@media only screen and (max-width: 450px) {
    main {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    .home-cards {
        grid-template-rows: 150px 150px;
        grid-gap: 3rem 3px;
    }

    .card-body {
        padding: 0;
    }

    .search-wrapper > .search-input {
        width: 280px;
    }
    
    .search-input {
        width: 340px;
    }
    .pagination-content {
        display: none;
    }
}

@media only screen and (max-width: 425px) {
    
    .search-input {
        width: 385px;
    }
}

@media only screen and (max-width: 375px) {
    
    .search-input {
        width: 340px;
    }
}

@media only screen and (max-width: 320px) {
    .search-input {
        width: 280px;
    }
}